*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 16px; }

body {
  font-family: "Montserrat", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  line-height: 1.2;
  background-color: #F6F6F6; }

a {
  text-decoration: none; }

img {
  height: auto;
  max-width: 100%; }

h1,
h2,
h3,
h4,
p,
figure,
fieldset,
ul {
  margin: 0; }

ul {
  padding-left: 0; }

li {
  list-style: none; }

input,
button,
select,
a,
textarea,
label {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

fieldset {
  border: none;
  padding: 0; }

button:focus,
input:focus,
a:focus {
  outline: none; }

button {
  border: none;
  padding: 0; }

.container {
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
  max-width: 1140px; }
  @media screen and (max-width: 768px) {
    .container {
      padding-right: 64px;
      padding-left: 64px; } }
  @media screen and (max-width: 600px) {
    .container {
      padding-right: 21px;
      padding-left: 21px; } }

.hover-opacity {
  transition: 0.2s; }
  .hover-opacity:hover {
    opacity: 0.5; }

.tablet-visibility {
  display: none; }
  @media screen and (max-width: 768px) {
    .tablet-visibility {
      display: block; } }

@media screen and (max-width: 768px) {
  .tablet-hidden {
    display: none; } }

.phone-visibility {
  display: none; }
  @media screen and (max-width: 600px) {
    .phone-visibility {
      display: block; } }

@media screen and (max-width: 600px) {
  .phone-hidden {
    display: none; } }

.main-footer {
  margin-top: auto;
  padding-left: 40px;
  background-color: #84e91e; }

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 56px;
  padding-bottom: 56px; }

.header__logo {
  margin-right: 120px; }

.header__nav {
  width: 100%; }

.container--header {
  max-width: 1656px; }

.nav__list {
  display: flex;
  justify-content: space-between;
  position: relative; }

.nav__item {
  background-color: transparent;
  border-left: none; }
  .nav__item--down-active .sub-list {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0); }

.nav__link {
  display: block;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  letter-spacing: 0.05em;
  text-align: left;
  color: #202020;
  padding: 5px 0;
  text-transform: uppercase;
  position: relative; }
  .nav__link::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    background-color: #202020;
    bottom: 0;
    left: 0;
    transform: translateY(0);
    transition: 0.5s;
    opacity: 0; }
  .nav__link:hover::after, .nav__link:focus::after {
    opacity: 1;
    transform: translateY(0); }
  .nav__link--down {
    display: flex;
    align-items: center; }
    .nav__link--down:hover::after, .nav__link--down:focus::after {
      content: none; }
  .nav__link--tel {
    font-size: 20px;
    color: #000; }

.nav__link-hamburger__icon {
  margin-right: 12px; }

[data-accordion] [data-content] {
  overflow: auto;
  max-height: none; }

[data-control],
[data-content] > * {
  padding: 0;
  border: none; }

[data-accordion] [data-control] {
  padding: 5px 0; }

.sub-list {
  position: absolute;
  right: 50px;
  top: 100%;
  width: 100vw;
  max-width: 565px;
  display: flex;
  flex-wrap: wrap;
  background-color: #718753;
  padding: 23px 100px;
  box-shadow: 0px 4px 10px rgba(95, 95, 95, 0.1);
  opacity: 0;
  transition: opacity 0.5s, transform 0.3s;
  pointer-events: none;
  transform: translateY(-10px); }
  .sub-list__item {
    width: 50%; }
    .sub-list__item:nth-child(even) {
      padding-left: 30px; }
  .sub-list__link {
    letter-spacing: 0.02em;
    font-size: 16px;
    font-weight: 300;
    color: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    display: inline-block;
    position: relative; }
    .sub-list__link::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: #fff;
      bottom: 0;
      left: 0;
      transform: translateY(-3px);
      transition: 0.5s;
      opacity: 0; }
    .sub-list__link:hover::after, .sub-list__link:focus::after {
      opacity: 1; }

ul.slimmenu:before,
ul.slimmenu:after {
  content: none; }
